function isEmpty(obj) {
	if (obj == null || obj == undefined || obj == '') {
		return true;
	} else {
		return false;
	}
}


function urlToJSON(url) {
	var params = url.split("?")[1];
	var paramArr = params.split('&');
	var res = {};
	for (var i = 0; i < paramArr.length; i++) {
		var str = paramArr[i].split('=');
		res[str[0]] = str[1];
	}
	return res;

}

function getNowDate() {
	var date = new Date();
	var seperator1 = "-";
	var month = date.getMonth() + 1;
	var strDate = date.getDate();
	if (month >= 1 && month <= 9) {
		month = "0" + month;
	}
	if (strDate >= 0 && strDate <= 9) {
		strDate = "0" + strDate;
	}
	var currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate;
	return currentdate
}

function formatDate(dateTime) {
	if (isEmpty(dateTime)) {
		return ""
	} else {
		var date = dateTime.replace(/\//g, '-');
		date = date.replace("T", " ");
		var arr = date.split(" ");
		return arr[0];
	}
}


function getNowDateTime() {
	var date = new Date();
	var seperator1 = "-";
	var month = date.getMonth() + 1;
	var strDate = date.getDate();
	if (month >= 1 && month <= 9) {
		month = "0" + month;
	}
	if (strDate >= 0 && strDate <= 9) {
		strDate = "0" + strDate;
	}

	var myHours = date.getHours() // Ëé∑ÂèñÂΩìÂâçÂ∞èÊó∂(0-23)	
	var myMinu = date.getMinutes() // Ëé∑ÂèñÂΩìÂâçÂàÜÈíü(0-59)	
	var mySec = date.getSeconds() // Ëé∑ÂèñÂΩìÂâçÁßíÊï∞(0-59)

	if (myHours >= 0 && myHours <= 9) {
		myHours = "0" + myHours;
	}

	if (myMinu >= 0 && myMinu <= 9) {
		myMinu = "0" + myMinu;
	}


	if (mySec >= 0 && mySec <= 9) {
		mySec = "0" + mySec;
	}

	var currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate + " " + myHours + ":" + myMinu + ":" +
		mySec;
	return currentdate
}






function dateCompare(date_1, date_2) {
	var date1 = new Date(date_1)
	var date2 = new Date(date_2)
	var s1 = date1.getTime(),
		s2 = date2.getTime();
	var total = (s2 - s1) / 1000;
	var day = parseInt(total / (24 * 60 * 60)); //ËÆ°ÁÆóÊï¥Êï∞Â§©Êï∞	
	var afterDay = total - day * 24 * 60 * 60; //ÂèñÂæóÁÆóÂá∫Â§©Êï∞ÂêéÂâ©‰ΩôÁöÑÁßíÊï∞	
	var hour = parseInt(afterDay / (60 * 60)); //ËÆ°ÁÆóÊï¥Êï∞Â∞èÊó∂Êï∞	
	var afterHour = total - day * 24 * 60 * 60 - hour * 60 * 60; //ÂèñÂæóÁÆóÂá∫Â∞èÊó∂Êï∞ÂêéÂâ©‰ΩôÁöÑÁßíÊï∞	
	var min = parseInt(afterHour / 60); //ËÆ°ÁÆóÊï¥Êï∞ÂàÜ	
	var afterMin = total - day * 24 * 60 * 60 - hour * 60 * 60 - min * 60; //ÂèñÂæóÁÆóÂá∫ÂàÜÂêéÂâ©‰ΩôÁöÑÁßíÊï∞


	var item = {
		total: total,
		day: day,
		hour: hour,
		min: min
	}

	return item
}


function getDateJSON(val) {
	var date = new Date(val);

	var weekList = ["Âë®Êó•", "Âë®‰∏Ä", "Âë®‰∫å", "Âë®‰∏â", "Âë®Âõõ", "Âë®‰∫î", "Âë®ÂÖ≠"]

	var item = {
		year: date.getFullYear(),
		month: date.getMonth() + 1,
		day: date.getDate(),
		week: date.getDay(),
		weekStr: weekList[date.getDay()],
		hour: date.getHours(),
		min: date.getMinutes(),
		sec: date.getSeconds()
	}
	console.log(item)
	return item;

}



function getWeek(date) {
	var week;
	if (date.getDay() == 0) week = 'Âë®Êó•';
	if (date.getDay() == 1) week = 'Âë®‰∏Ä';
	if (date.getDay() == 2) week = 'Âë®‰∫å';
	if (date.getDay() == 3) week = 'Âë®‰∏â';
	if (date.getDay() == 4) week = 'Âë®Âõõ';
	if (date.getDay() == 5) week = 'Âë®‰∫î';
	if (date.getDay() == 6) week = 'Âë®ÂÖ≠';
	return week;
}


