//Âà§Êñ≠ËØ∑Ê±ÇÁöÑÊï∞ÊçÆsuccess
function CheckData(res) {
	if (res.statusCode != 200) {
		if (res.statusCode == 401) {
			uni.clearStorageSync('token');
			uni.navigateTo({
				url: '/pages/sign/sign'
			})
		}
		return false;
	} else {
		if (res.data.errorCode == 0) {
			return true;
		} else {
			return false
		}
	}
}



